import React, { useEffect, useState } from "react";
import { useNavigate } from 'react-router-dom';
import axios from "axios";

export default function OrderHistory() {
const navigate = useNavigate()
const [tickets, setTickets] = useState();


const order_history = async () => {
    return await axios.get(
      `https://nightlife-2710.herokuapp.com/user-ticket-history?access_token=${sessionStorage.token}`
      );
    };

    useEffect(() => {
        order_history()
        .then((response) => {
          setTickets(response?.data?.Ticket_history)
          console.log(response?.data?.Ticket_history)
        })
        .catch((error) => {
          console.log(error);
        });
        //eslint-disable-next-line
      }, []);

      return (
        <div>
          <nav className='w-100 bg-light'>
            <i className="fa-solid fa-arrow-left m-3" onClick={()=>navigate("/")} style={{fontSize: "30px"}}></i>
          </nav>
          <div className='mb-5 mb-2 mb-md-3 py-5' style={{ background: "black"}}>
            <div className='d-flex justify-content-center align-items-center flex-column px-md-5 px-2 mx-lg-4 mx-md-2' style={{height: "100%"}}>
              <div className='primary-header ml-2 text-white text-center' style={{fontSize: "40px"}}>Your Orders</div>
            </div>
          </div>
          {/* <div>
            {tickets?.reduce((acc, ticket) => {
              console.log(acc)
              const date = ticket?.date;
              if (!acc[date]) {
                acc[date] = {
                  date: date,
                  tickets: [ticket],
                };
              } else {
                acc[date].tickets.push(ticket);
              }
              return acc;
            }, {})
            .values()
            .map((ticketGroup) => (
              <div key={ticketGroup.date}>
                <h2>{ticketGroup.date}</h2>
                {ticketGroup.tickets.map((ticket) => (
                  <div key={ticket.event_id}>
                    <p>{ticket.event_name}</p>
                    {ticket?.orders.map((order) => (
                      <p key={order.order_id}>{order.order_id}</p>
                    ))}
                  </div>
                ))}
              </div>
            ))}
          </div> */}
        </div>
      );
      
}


